<template>
    <div class="gv-panel-title gv-color-scheme">
        <span :title="fullTitle" class="gv-color-scheme">
            {{title}}
        </span>
        <button class="gv-close gv-color-scheme" type="button" @click="closePanel">×</button>
    </div>
</template>

<script>
import Vue from 'vue'

export default {
  name: 'gv-title',
  props: ['divId', 'title', 'fullTitle', 'hide'],
  methods: {
    closePanel: function() {
      let div = document.getElementById(this.divId)
      if (div) {
        this.hide ? (div.style.display = 'none') : div.parentNode.removeChild(div)
      } 
    //   else {
    //     div = this.$el.parentNode  
    //     this.hide
    //       ? (div.style.display = 'none')
    //       : div.parentNode.removeChild(this.$el.parentNode)
    //   }
    },
  },
  mounted: function() {},
}
</script>

<style scoped>
.gv-panel-title {
  position: relative;
  display: block;
  padding: 0.3rem 0.5rem;
  margin-bottom: -1px;
  color: #ccc;
  cursor: default;
  font-weight: bold;
}

.gv-panel-title span {
  font-size: 14px;
  font-weight: bold;
  height: 20px;
}

.gv-close {
  padding: 0;
  cursor: pointer;
  background: transparent;
  border: 0;
  -webkit-appearance: none;
  float: right;
  font-size: 1rem;
  line-height: 1;
  font-weight: bold;
  color: #ffffff;
  margin-left: 5px;
  margin-right: 3px;
  margin-top: 3px;
  opacity: 0.5;
}
</style>


